.App {
  text-align: center;
  font-family: sans-serif;
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  border-radius: 8px 8px 0 0;
}

.App-header h1 {
  margin: 0;
}

main {
  padding: 20px;
}

.participants-input h2, .results h2 {
  margin-top: 0;
  color: #333;
}

.participant-entry {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.participant-entry input[type="text"] {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1em;
}

.remove-btn, .add-btn {
  padding: 10px 15px;
  margin-left: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9em;
}

.remove-btn {
  background-color: #f44336; /* Red */
  color: white;
}

.add-btn {
  background-color: #4CAF50; /* Green */
  color: white;
  display: block; /* Make it take its own line or adjust layout */
  margin-top: 10px; /* Spacing from the last input */
}

.randomize-btn, .copy-btn {
  background-color: #007bff; /* Blue */
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  font-size: 1.1em;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.2s;
}

.randomize-btn:hover, .copy-btn:hover {
  background-color: #0056b3;
}

.randomize-btn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.error-message {
  color: red;
  margin-top: 15px;
  font-weight: bold;
}

.results {
  margin-top: 30px;
  text-align: left;
  background-color: #fff;
  padding: 15px;
  border-radius: 4px;
  border: 1px solid #eee;
}

.results ol {
  list-style-position: inside;
  padding-left: 0;
}

.results li {
  padding: 8px 0;
  border-bottom: 1px solid #f0f0f0;
  font-size: 1.1em;
}
.results li:last-child {
  border-bottom: none;
}

.copy-btn {
  display: block;
  margin-top: 15px;
  background-color: #5cb85c; /* A different green for copy */
}
.copy-btn:hover {
  background-color: #4cae4c;
}

/* Spinner CSS */
.spinner-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border-left-color: #007bff; /* Blue */
  animation: spin 1s ease infinite;
  margin-bottom: 10px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* ... (your existing App.css) ... */

.participants-management {
  margin-bottom: 20px;
  padding: 15px;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #eee;
}

.add-participant-controls {
  display: flex;
  align-items: center;
  gap: 10px; /* Space between select and button */
  margin-bottom: 15px;
}

.add-participant-controls select {
  flex-grow: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1em;
}

.current-participants {
  margin-top: 15px;
}
.current-participants h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1.1em;
  color: #555;
}
.current-participants ul {
  list-style-type: none;
  padding: 0;
}
.current-participants li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  border-bottom: 1px solid #f0f0f0;
}
.current-participants li:last-child {
  border-bottom: none;
}

.remove-btn-small {
  padding: 5px 10px;
  font-size: 0.8em;
  background-color: #ef5350; /* Lighter red */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.remove-btn-small:hover {
  background-color: #e53935;
}

/* Ensure add-btn is styled if it wasn't generic enough */
.add-btn {
  padding: 10px 15px;
  background-color: #4CAF50; /* Green */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em; /* Match select input */
}
.add-btn:disabled {
  background-color: #ccc;
}

